import { useContext, useState } from "react";
import { BiSearchAlt } from "react-icons/bi";
import AppContext from "../context/app.context";
import Results from "./Results";

const Seeker = () => {
  const { topics, setResultOpen } = useContext(AppContext);
  const [value, setValue] = useState("");
  const [results, setResults] = useState([]);
  const handleChange = (e) => {
    setValue(e.target.value);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!value) return setResultOpen(false);
    const topicsMatch = topics.filter((topic) => {
      let results = topic.temmary.filter((tem) =>
        tem.title.toLowerCase().includes(value.toLowerCase().trim())
      );
      return results.length ? true : false;
    });
    setResultOpen(true);
    setResults(
      value
        ? topicsMatch.map((top) => ({
            ...top,
            temmary: top.temmary.filter((tem) =>
              tem.title.toLowerCase().includes(value.toLowerCase().trim())
            ),
          }))
        : []
    );
  };
  return (
    <form className="seeker" onSubmit={handleSubmit}>
      <input
        type="text"
        placeholder="Buscar"
        value={value}
        required
        onChange={handleChange}
      />
      <input style={{ display: "none" }} type="submit" id="submit" />
      <div className="seeker-line"></div>
      <label htmlFor="submit" className="seeker-button">
        <BiSearchAlt />
      </label>
      <Results results={results} />
    </form>
  );
};

export default Seeker;
