import { useParams } from "react-router";
import { useState, useEffect, useRef } from "react";
import getMD from "../helpers/getMD";
import mdToHtml from "../helpers/mdToHtml";

const Content = () => {
  const { url } = useParams();
  const [md, setMd] = useState("");
  const content = useRef();

  useEffect(() => {
    const getContent = async () => {
      setMd(await getMD(`./topics/${url}.md`));
    };
    getContent();

    const title = url.split("-").join(" ");
    document.title = title[0].toUpperCase() + title.slice(1);
    document
      .querySelectorAll(".topic[open]")
      ?.forEach((el) => el.removeAttribute("open"));
    document
      .querySelector(".temmary-link.active")
      .closest(".topic")
      .setAttribute("open", "");
  }, [url]);

  useEffect(() => {
    let html = mdToHtml(md);
    content.current.innerHTML = html;
    document.querySelector(".main-wrapper").scrollTop = 0;
  }, [md]);
  return <div ref={content}></div>;
};

export default Content;
