import { useEffect, useState } from "react";
import { BiMoon, BiSun } from "react-icons/bi";

const ls = localStorage;
if (ls.getItem("theme") === null) ls.setItem("theme", JSON.stringify(true));

export const ThemeToggle = () => {
  const [isLight, setIsLight] = useState(JSON.parse(ls.getItem("theme")));

  useEffect(() => {
    isLight
      ? (document.documentElement.className = "html ")
      : (document.documentElement.className = "html dark");
    ls.setItem("theme", isLight);
  }, [isLight]);

  return (
    <button
      className="button-circle theme-toggle"
      onClick={() => setIsLight(!isLight)}>
      {isLight ? <BiMoon /> : <BiSun />}
    </button>
  );
};
