import { useContext } from "react";
import { BiX } from "react-icons/bi";
import { Link } from "react-router-dom";
import AppContext from "../context/app.context";

const Results = ({ results }) => {
  const { resultOpen, closeResults } = useContext(AppContext);
  return (
    <div className={`results-box ${resultOpen ? "open" : ""}`}>
      {results.length ? (
        results.map((result, i) => (
          <div className="results-topic" key={i}>
            <h3 className="results-topic_title">{result.title}</h3>
            {result.temmary.map((temmary, id) => (
              <div className="result-temmary" key={id}>
                <Link
                  className="result-link"
                  to={temmary.url}
                  onClick={closeResults}>
                  {temmary.title}
                </Link>
              </div>
            ))}
          </div>
        ))
      ) : (
        <div className="results-404">
          No se encontraron temas con ese termino
        </div>
      )}
      <div className="results-close" onClick={closeResults}>
        <BiX />
      </div>
    </div>
  );
};

export default Results;
