import { BiChevronDown } from "react-icons/bi";
import Temmary from "./Temmary";

const Topic = ({ topic, handleClick }) => {
  return (
    <details className="topic">
      <summary className="topic-title">
        {topic.title} <BiChevronDown />
      </summary>
      {topic.temmary.map((temmary, index) => (
        <Temmary key={index} temmary={temmary} handleClick={handleClick} />
      ))}
    </details>
  );
};

export default Topic;
