import { useEffect, useRef } from "react";
import { NavLink } from "react-router-dom";

const Temmary = ({ temmary, handleClick }) => {
  const link = useRef();
  useEffect(() => {
    if (link.current.classList.contains("active")) {
      link.current.closest(".topic").setAttribute("open", "");
    }
  });
  return (
    <NavLink
      ref={link}
      activeClassName="active"
      className="temmary-link"
      onClick={handleClick}
      exact
      to={`/${temmary.url}`}>
      {temmary.title}
    </NavLink>
  );
};

export default Temmary;
